.recommend {
	padding: 70px 0 0;
	position: relative;
	z-index: 2;
	@include sm {
		padding: 35px 0 0; }
	&__title {
		text-align: center;
		margin-bottom: 60px;
		@include sm {
			margin-bottom: 30px; } } }

.slider-recm {
    overflow: hidden;
    &__wrap {
        position: relative;
        white-space: nowrap;
        opacity: 0;
        visibility: hidden;
        transition: opacity .5s, visibility .5s;
        font-size: 0;
        &.slick-initialized {
            overflow: visible;
            opacity: 1;
            visibility: visible; }
        .slick-list.draggable {
            margin-bottom: 20px; } }
    &__slide {
        display: block !important;
        width: 100%;
        height: 0;
        padding-bottom: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
        text-align: center;
        font-size: 0;
        margin-top: -2px;
        img {
            display: inline-block;
            width: 80%; }
        @include sm {
            img {
                width: 80%; } } }
    .slick-list.draggable {
        padding: 0 !important; }
    .slick-slide.slick-current.slick-active.slick-center {
        min-width: 290px; }

    .slick-dots {
        display: flex;
        align-items: center;
        justify-content: center;
        button {
            font-size: 0; }
        li {
            position: relative;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            border: 2px solid $red;
            box-sizing: border-box;
            cursor: pointer;
            &:not(:last-child) {
                margin-right: 20px; }
            &:after {
                position: absolute;
                content: '';
                width: 10px;
                height: 10px;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                border-radius: 50%;
                background-color: $red;
                opacity: 0;
                transition: opacity .3s; }
            &.slick-active {
                &:after {
                    opacity: 1; } } }
        @include sm {
            li {
                width: 14px;
                height: 14px;
                &:after {
                    width: 7px;
                    height: 7px; } } } }
