.slider__prev-btn, .slider__next-btn { position: absolute; top: 50%; width: 20px; height: 40px; z-index: 2; background: none; background-image: url(../img/next.png); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

@media only screen and (max-width: 767px) { .slider__prev-btn, .slider__next-btn { width: 10px; height: 20px; } }

.slider__prev-btn:hover:after, .slider__next-btn:hover:after { background-color: rgba(255, 255, 255, 0.5); }

.slider__prev-btn { left: 50px; transform: translate(-50%, -50%) rotate(180deg); }

.slider__prev-btn:after { position: absolute; top: -10px; left: -24px; content: ''; width: 60px; height: 60px; background-color: rgba(255, 255, 255, 0.3); border-radius: 50%; transition: background-color .3s; }

@media only screen and (max-width: 767px) { .slider__prev-btn { left: 20px; }
  .slider__prev-btn:after { width: 30px; height: 30px; top: -5px; left: -10px; } }

.slider__next-btn { transform: translate(50%, -50%); right: 50px; }

.slider__next-btn:after { position: absolute; top: -10px; left: -24px; content: ''; width: 60px; height: 60px; background-color: rgba(255, 255, 255, 0.3); border-radius: 50%; transition: background-color .3s; }

@media only screen and (max-width: 767px) { .slider__next-btn { right: 20px; }
  .slider__next-btn:after { width: 30px; height: 30px; top: -5px; left: -10px; } }

.slider__slide { width: 100%; height: 0; padding-bottom: 50%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.slider__slide--mod { background-position: 50% 10%; }

.contacts { padding: 70px 0 0; position: relative; z-index: 2; border-top: 2px solid #e80707; }

@media only screen and (max-width: 767px) { .contacts { padding: 35px 0 0; } }

.contacts__title { text-align: center; margin-bottom: 60px; color: #010101; }

@media only screen and (max-width: 767px) { .contacts__title { margin-bottom: 30px; } }

.contacts__wrap { display: flex; justify-content: space-between; }

@media only screen and (max-width: 767px) { .contacts__wrap { flex-wrap: wrap; justify-content: center; align-items: center; flex-direction: column; } }

.contacts__block { flex: 0 0 25%; margin-right: 20px; color: #010101; margin-bottom: 25px; }

@media only screen and (max-width: 767px) { .contacts__block { flex: 0 0 100%; margin-right: 0; text-align: center; } }

.contacts__block-title { font-size: 22px; font-weight: 400; margin-bottom: 20px; }

@media only screen and (max-width: 767px) { .contacts__block-title { font-size: 18px; margin-bottom: 15px; } }

.contacts__block-list { padding-left: 30px; font-size: 16px; }

@media only screen and (max-width: 767px) { .contacts__block-list { font-size: 14px; } }

.contacts__block-item { position: relative; margin-bottom: 10px; }

@media only screen and (max-width: 767px) { .contacts__block-item { text-align: center; } }

.contacts__link { text-decoration: none; color: #010101; }

.contacts__icon { position: absolute; top: 0px; left: -30px; font-size: 22px; }

.contacts__icon .icon { fill: #e80707; }

.comments { padding-bottom: 100px; }

@media only screen and (max-width: 767px) { .comments { padding-bottom: 50px; } }

.comments .header { padding-top: 20px; padding-bottom: 20px; margin-bottom: 80px; }

@media only screen and (max-width: 767px) { .comments .header { margin-bottom: 50px; } }

.comments .header__logo { top: 0; }

.comments__inner { position: relative; z-index: 2; }

.comments__title { text-align: center; margin-bottom: 70px; }

@media only screen and (max-width: 767px) { .comments__title { letter-spacing: .1px; margin-bottom: 35px; } }

.comments__hero { padding-bottom: 100px; }

@media only screen and (max-width: 767px) { .comments__hero { padding-bottom: 50px; } }

.comments__hero-btn { text-align: center; }

.comments__hero-link { display: inline-block; font-size: 20px; text-transform: uppercase; font-weight: 600; padding: 14px 50px; }

@media only screen and (max-width: 767px) { .comments__hero-link { font-size: 14px; padding: 10px 25px; } }

.comments__form { padding: 70px 0; }

@media only screen and (max-width: 767px) { .comments__form { padding: 35px 0; } }

.comments__form-title { text-align: center; margin-bottom: 50px; }

@media only screen and (max-width: 767px) { .comments__form-title { letter-spacing: .1px; margin-bottom: 25px; } }

.comments__container { max-width: 800px; margin: 0 auto; }

.comment-form__wrap { display: flex; justify-content: space-between; }

.comment-form__wrap .comment-form__row:first-child { margin-right: 20px; }

@media only screen and (max-width: 767px) { .comment-form__wrap .comment-form__row:first-child { margin-right: 0; } }

@media only screen and (max-width: 767px) { .comment-form__wrap { flex-wrap: wrap; } }

.comment-form__row { width: 100%; }

.comment-form__row--mod { text-align: center; margin-top: 30px; }

.comment-form__input, .comment-form__textarea { width: 100%; padding: 10px 15px; border: none; outline: none; margin-bottom: 20px; background-color: transparent; border-bottom: 2px solid #000000; font-family: "Roboto"; font-size: 16px; color: #010101; }

@media only screen and (max-width: 767px) { .comment-form__input, .comment-form__textarea { font-size: 14px; } }

.comment-form__input::placeholder, .comment-form__textarea::placeholder { font-family: "Roboto"; font-size: 16px; color: #999999; }

@media only screen and (max-width: 767px) { .comment-form__input::placeholder, .comment-form__textarea::placeholder { font-size: 14px; } }

.comment-form__textarea { min-height: 100px; resize: none; }

.comment-form__btn { font-weight: 600; text-transform: uppercase; padding: 10px 50px; color: #e80707; transition: color .3s; }

.comment-form__btn:hover { color: #ffffff; font-weight: 600; }

@media only screen and (max-width: 767px) { .comment-form__btn:hover { color: #e80707; } }

.comment-card:not(:last-child) { margin-bottom: 30px; }

.comment-card__contain { display: flex; align-items: flex-start; padding: 25px 30px; border-radius: 10px; box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); }

@media only screen and (max-width: 767px) { .comment-card__contain { padding: 15px 20px; } }

@media only screen and (max-width: 599px) { .comment-card__contain { flex-wrap: wrap; align-items: center; } }

.comment-card__avatar { flex: 1 1 100px; margin-right: 30px; border-radius: 50%; max-width: 100px; min-width: 100px; height: 100px; text-align: center; font-size: 0; line-height: 120px; background-color: #999999; }

.comment-card__avatar img { vertical-align: middle; width: 80%; }

@media only screen and (max-width: 599px) { .comment-card__avatar { margin: 0 auto 15px; } }

.comment-card__text { flex: 1 1 100%; text-decoration: none; font-size: 16px; line-height: 1.5; color: #010101; }

@media only screen and (max-width: 767px) { .comment-card__text { font-size: 14px; } }

.comment-card__wrap { display: flex; align-items: center; justify-content: space-between; margin-bottom: 20px; }

@media only screen and (max-width: 767px) { .comment-card__wrap { flex-wrap: wrap; } }

.comment-card__name, .comment-card__date { font-size: 14px; font-weight: 600; color: #999999; }

.comment-card__name { margin-right: 20px; font-size: 18px; }

.comment-card__name .comment-card__link { font-size: 18px; font-weight: 600; color: #999999; text-decoration: none; border-bottom: 1px solid transparent; transition: border-color .3s; }

.comment-card__name .comment-card__link:hover { border-bottom: 1px solid #999999; }

.recommend { padding: 70px 0 0; position: relative; z-index: 2; }

@media only screen and (max-width: 767px) { .recommend { padding: 35px 0 0; } }

.recommend__title { text-align: center; margin-bottom: 60px; }

@media only screen and (max-width: 767px) { .recommend__title { margin-bottom: 30px; } }

.slider-recm { overflow: hidden; }

.slider-recm__wrap { position: relative; white-space: nowrap; opacity: 0; visibility: hidden; transition: opacity .5s, visibility .5s; font-size: 0; }

.slider-recm__wrap.slick-initialized { overflow: visible; opacity: 1; visibility: visible; }

.slider-recm__wrap .slick-list.draggable { margin-bottom: 20px; }

.slider-recm__slide { display: block !important; width: 100%; height: 0; padding-bottom: 100%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; text-align: center; font-size: 0; margin-top: -2px; }

.slider-recm__slide img { display: inline-block; width: 80%; }

@media only screen and (max-width: 767px) { .slider-recm__slide img { width: 80%; } }

.slider-recm .slick-list.draggable { padding: 0 !important; }

.slider-recm .slick-slide.slick-current.slick-active.slick-center { min-width: 290px; }

.slider-recm .slick-dots { display: flex; align-items: center; justify-content: center; }

.slider-recm .slick-dots button { font-size: 0; }

.slider-recm .slick-dots li { position: relative; width: 20px; height: 20px; border-radius: 50%; border: 2px solid #e80707; box-sizing: border-box; cursor: pointer; }

.slider-recm .slick-dots li:not(:last-child) { margin-right: 20px; }

.slider-recm .slick-dots li:after { position: absolute; content: ''; width: 10px; height: 10px; top: 50%; left: 50%; transform: translate(-50%, -50%); border-radius: 50%; background-color: #e80707; opacity: 0; transition: opacity .3s; }

.slider-recm .slick-dots li.slick-active:after { opacity: 1; }

@media only screen and (max-width: 767px) { .slider-recm .slick-dots li { width: 14px; height: 14px; }
  .slider-recm .slick-dots li:after { width: 7px; height: 7px; } }
